import React, {useEffect} from 'react';
import './App.css';
import {RouterProvider} from 'react-router-dom';
import {router} from "./routes";
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import {Container} from "@mui/material";
import {LocalStorageManager} from "./store";
import {STORE} from "./store/LocalStorageManager";

function App() {

    useEffect(() => {
        const storedPlayers = LocalStorageManager.getItem(STORE.PLAYERS);
        if(storedPlayers == null || storedPlayers.length === 0) {
            LocalStorageManager.setItem(STORE.PLAYERS, ['Sai Ashok', 'Anil', 'Chenchu', 'Srinivas']);
        }
        //const storedGamePlayers = localStorage.getItem('game-players');
    }, []);



    return (
        <Box sx={{flexGrow: 1}}>
            <AppBar position="static">
                <Toolbar>
                    <Typography align="center" variant="h6" component="div" sx={{flexGrow: 1}}>
                        Badminton Scheduler
                    </Typography>
                </Toolbar>
            </AppBar>
            <Container maxWidth="md" className={"page-container"}>
                <RouterProvider router={router}/>
            </Container>
        </Box>
    );
}

export default App;
