export interface GroupGames {
    InPlay: string[];
    OnBreak: string;
}

class Game {
    players: string[];
    sittingOut: string;

    constructor(players: string[], sittingOut: string) {
        this.players = players;
        this.sittingOut = sittingOut;
    }

    playGame() {
        const playersInGame = this.players.filter(player => player !== this.sittingOut);
        console.log(`Game: ${playersInGame.join(', ')} vs ${this.sittingOut}`);
        return {
            InPlay: playersInGame,
            OnBreak: this.sittingOut
        };
    }
}

export class FivePlayerScheduler {
    players: string[];

    constructor(players: string[]) {
        this.players = players;
    }

    generateSchedule(): GroupGames[] {
        const games: GroupGames[] = [];
        for (let i = 0; i < this.players.length; i++) {
            const sittingOut = this.players[i];
            const playersInGame = this.players.filter(player => player !== sittingOut);
            const game = new Game(playersInGame, sittingOut);
            console.log(`Game ${i + 1}:`);
            const groupGame = game.playGame();
            games.push(groupGame);
            console.log("------------");
        }
        return games;
    }
}

