import React, {useEffect, useState} from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import { Breadcrumbs, Button, Checkbox, FormControlLabel,
    Grid, Link, Dialog, DialogTitle, DialogContent,
    TextField, DialogActions, IconButton } from "@mui/material";
import { ROUTE_PATHS } from "../routes";
import {FivePlayerScheduler} from "../core/FivePlayerScheduler";
import {useNavigate} from "react-router-dom";
import {LocalStorageManager} from "../store";
import DeleteIcon from '@mui/icons-material/Delete';
import {STORE} from "../store/LocalStorageManager";


const SchedulerPage: React.FC = () => {

    const navigate = useNavigate();

    useEffect(() => {
        const storedPlayers = LocalStorageManager.getItem(STORE.PLAYERS) || [];
        const playersInGame = LocalStorageManager.getItem(STORE.SCHEDULED_PLAYERS) || [];
        let filteredPlayers = storedPlayers;
        if(playersInGame.length > 0) {
            filteredPlayers = storedPlayers.filter((player: string) => !playersInGame.includes(player));
        }
        if (storedPlayers) {
            setPlayers(filteredPlayers);
        }
        const storedGamePlayers = LocalStorageManager.getItem(STORE.GAME_PLAYERS);
        if (storedGamePlayers) {
            setGamePlayers(storedGamePlayers);
        }
    }, []);

    const [players, setPlayers] = useState<string[]>([]);
    const [gamePlayers, setGamePlayers] = useState<string[]>([]);
    const [open, setOpen] = useState<boolean>(false);
    const [newPlayerName, setNewPlayerName] = useState<string>('');

    const handleCheckboxChange = (playerName: string) => {
        const index = gamePlayers.indexOf(playerName);
        if (index === -1) {
            setGamePlayers([...gamePlayers, playerName]);
            LocalStorageManager.setItem(STORE.GAME_PLAYERS, [...gamePlayers, playerName]);
        } else {
            const updatedPlayers = [...gamePlayers];
            updatedPlayers.splice(index, 1);
            setGamePlayers(updatedPlayers);
            LocalStorageManager.setItem(STORE.GAME_PLAYERS, updatedPlayers);
        }
    };

    const handleAddPlayer = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleAddNewPlayer = () => {
        if (newPlayerName.trim() !== '') {
            if (players.includes(newPlayerName)) {
                alert(`Player "${newPlayerName}" already exists!`);
            } else {
                setPlayers([...players, newPlayerName]);
                LocalStorageManager.setItem(STORE.PLAYERS, [...players, newPlayerName]);
                setNewPlayerName('');
                handleClose();
            }
        }
    };

    const handleCreateGames = () => {
        console.log(gamePlayers);

        if(gamePlayers.length !== 5) {
            alert("Only 5 players allowed for creating games per group / court");
            return;
        }

        const games = new FivePlayerScheduler(gamePlayers);
        const schedule = games.generateSchedule();

        // Retrieve existing schedule from local storage
        let existingSchedule = LocalStorageManager.getItem(STORE.GAMES);
        if (!existingSchedule) {
            existingSchedule = [];
        }
        // Add the new schedule to existing schedule
        existingSchedule.push(schedule);

        LocalStorageManager.setItem(STORE.SCHEDULED_PLAYERS, gamePlayers);
        LocalStorageManager.setItem(STORE.GAMES, existingSchedule);
        navigate(ROUTE_PATHS.home);
    }

    const handleDeletePlayer = (playerName: string) => {
        const updatedPlayers = players.filter(player => player !== playerName);
        setPlayers(updatedPlayers);
        localStorage.setItem('players', JSON.stringify(updatedPlayers));
    };

    return (
        <div>
            <Box>
                <Breadcrumbs aria-label="breadcrumb">
                    <Link underline="hover" color="inherit" href={ROUTE_PATHS.home}>
                        Dashboard
                    </Link>
                    <Link href={ROUTE_PATHS.scheduler} color="text.primary">Games Scheduler</Link>
                </Breadcrumbs>
                <Typography color={'grey.900'} mb={1} mt={2}>
                    {'Select any 5 players for this group / court'}
                </Typography>
                <Grid container spacing={2}>
                    {players.map((player: string, index: number) => (
                        <Grid item xs={6} md={4} key={index}>
                            <div className={"checkbox-button"}>
                                <FormControlLabel
                                    sx={{
                                        flexGrow: '1'
                                    }}
                                    control={
                                        <Checkbox
                                            checked={gamePlayers.includes(player)}
                                            onChange={() => handleCheckboxChange(player)}
                                            inputProps={{ 'aria-label': 'controlled' }}
                                        />
                                    }
                                    label={player}
                                />
                                <IconButton className={"icon"}
                                    aria-label="delete" onClick={() => handleDeletePlayer(player)}>
                                    <DeleteIcon />
                                </IconButton>
                            </div>
                        </Grid>
                    ))}
                    <Grid item xs={6} md={4}>
                        <Button variant="outlined" onClick={handleAddPlayer}>Add Player</Button>
                    </Grid>
                </Grid>

                <Box mt={4} sx={{ textAlign: 'center' }}>
                    <Button variant="contained" onClick={handleCreateGames}>Create Games</Button>
                </Box>
            </Box>
            <Dialog open={open} onClose={handleClose} className={"custom-dialog"}>
                <DialogTitle>Add New Player</DialogTitle>
                <DialogContent>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Player Name"
                        type="text"
                        fullWidth
                        value={newPlayerName}
                        onChange={(e) => setNewPlayerName(e.target.value)}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Cancel</Button>
                    <Button onClick={handleAddNewPlayer}>Add</Button>
                </DialogActions>
            </Dialog>
        </div>
    );
};

export default SchedulerPage;
