interface StorageItem {
    key: string;
    value: any;
}

export const STORE = {
    PLAYERS: 'players',
    GAME_PLAYERS: 'game-players',
    SCHEDULED_PLAYERS: 'scheduled-players',
    GAMES: 'schedule'
}

export class LocalStorageManager {
    static getItem(key: string): any {
        const item = localStorage.getItem(key);
        if (item) {
            return JSON.parse(item);
        }
        return null;
    }

    static setItem(key: string, value: any): void {
        localStorage.setItem(key, JSON.stringify(value));
    }

    static removeItem(key: string): void {
        localStorage.removeItem(key);
    }

    static clear(): void {
        localStorage.clear();
    }

    static getItems(keys: string[]): StorageItem[] {
        return keys.map(key => ({ key, value: this.getItem(key) }));
    }

    static setItems(items: StorageItem[]): void {
        items.forEach(item => this.setItem(item.key, item.value));
    }

    static removeItems(keys: string[]): void {
        keys.forEach(key => this.removeItem(key));
    }
}