import React, {useEffect, useState} from "react";
import {useNavigate} from 'react-router-dom';
import {ROUTE_PATHS} from "../routes";
import Alert from '@mui/material/Alert';
import Box from "@mui/material/Box";
import {Button, Chip, Grid} from "@mui/material";
import {LocalStorageManager} from "../store";
import {STORE} from "../store/LocalStorageManager";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import {GroupGames} from "../core/FivePlayerScheduler";
import Typography from "@mui/material/Typography";

const HomePage: React.FC = () => {

    const [games, setGames] = useState([]);
    useEffect(() => {
        LocalStorageManager.setItem(STORE.GAME_PLAYERS, []);
        const scheduledGames = LocalStorageManager.getItem(STORE.GAMES) || [];
        setGames(scheduledGames);
    }, []);

    const navigate = useNavigate();

    const handleStartTournament = () => {
        navigate(ROUTE_PATHS.scheduler);
    }

    return (
        <div>
            {games.length > 0 && (
                <div>
                    {games.map((gamesMap: any, index: number) => (
                        <Card sx={{
                            marginBottom: '20px'
                        }}>
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="div">
                                    COURT {index + 1}
                                </Typography>
                                <Grid container spacing={2} key={index}>
                                    {gamesMap.map((game: GroupGames, index: number) => (
                                        <Grid item xs={6} md={4} key={index}>
                                            <div className={"game-box"}>
                                                <fieldset>
                                                    <legend>In Play</legend>
                                                    {game.InPlay.map((name: string) => (
                                                        <div className={"player-name"}>{name}</div>
                                                    ))}
                                                </fieldset>
                                            </div>
                                            <div className={"on-break"}>
                                                <span>On break :</span>
                                                <span>{game.OnBreak}</span>
                                            </div>
                                        </Grid>
                                    ))}
                                </Grid>
                            </CardContent>
                        </Card>
                    ))}
                </div>
            )}

            <Box display="flex" justifyContent="center" sx={{
                marginTop: '10px',
                marginBottom: '10px'
            }}>
                {games.length === 0 && (
                    <Alert
                        severity="info"
                        sx={{
                            width: '500px', // Adjust width as needed
                        }}
                    >
                        No games are planned.
                    </Alert>
                )}
            </Box>
            <Box sx={{
                textAlign: 'center'
            }}>
                <Button variant="outlined"
                        sx={{}}
                        onClick={handleStartTournament}>Schedule Games</Button>
            </Box>
        </div>
    );
}

export default HomePage;